import { useSession, signIn, signOut } from "next-auth/react";

import { useState } from "react";
import { useForm } from "react-hook-form";
import Account from "../types/account";
import authService from "../services/client/auth";
import SignInResponse from "../types/signInResponse";
import { useRouter } from "next/navigation";

function useAuth() {
    const { data: session } = useSession();
    const route = useRouter();
    const [message, setMessage] = useState<string | null>(null);
    const [isLoading, setIsLoading] = useState<boolean>(false);

    const { control, handleSubmit } = useForm<Account>({
        defaultValues: {
            email: "",
            password: "",
        },
    });

    const handleSignIn = handleSubmit(async (account: Account): Promise<void> => {
        setIsLoading(true);
        setMessage(null);
        const signIn: SignInResponse = await authService.signIn(account);
        if (signIn.authed) {
            route.push("/customer");
        } else {
            setMessage(signIn.description);
        }
        setIsLoading(false);
    });

    const handleSignOut = async () => {
        const signOut = await authService.signOut();
        console.info("signOut", signOut);
    };

    return { session, message, control, handleSignIn, handleSignOut, signIn, signOut, isLoading };
}

export default useAuth;
