import Account from "@/app/types/account";
import { AuthService } from "./auth";
import SignInResponse from "@/app/types/signInResponse";

export function createAuthServiceInstance(): AuthService {
    return new AuthServiceImpl();
}

class AuthServiceImpl implements AuthService {
    async signIn(account: Account): Promise<SignInResponse> {
        try {
            const response = await fetch("http://localhost:3000/api/auth/sign-in", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify(account),
            });

            if (!response.ok) {
                throw new Error(`HTTP error! Status: ${response.status}`);
            }

            const result = await response.json();
            return result;
        } catch (error) {
            throw new Error("Sign In failed " + error);
        }
    }

    async signOut(): Promise<any> {
        try {
            const response = await fetch("http://localhost:3000/api/auth/sign-out", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
            });

            if (!response.ok) {
                throw new Error(`HTTP error! Status: ${response.status}`);
            }

            const result = await response.json();
            return result;
        } catch (error) {
            throw new Error("Sign out failed " + error);
        }
    }
}
